:root {
  --first-color: #ff491b;
  --text-color: #ffffff;
  --body-color: #2b2d2f;
  --nav-color: #707070;
  --card-color: #3a3a3a;
  --navbar-active: #3a3a3a;
  --darkmode-button-color: #ffffff;
}

body {
  background-color: var(--body-color);
  color: var(--text-color);
}

.App {
  background-color: var(--background-color);
  color: var(--text-color);
}

.dark-mode {
  --first-color: #ff491b;
  --text-color: #000000;
  --body-color: #e9e9e9;
  --nav-color: #a3a3a3;
  --card-color: #bebebe;
  --navbar-active: #dfdfdf;
  --background-color: #e9e9e9;
}

.darkmodeToggle {
  background-color: var(--card-color);
  color: var(--text-color);
  padding: 16px 32px;
  margin: 1vh;
  border: none;
  cursor: pointer;
  font-size: 1.5rem;
  border-radius: 16px;
  position: absolute;
  z-index: 999;
}
