/* Main landing */
.header {
  text-align: center;
  padding: 1.5vh;
  border-bottom: 0.1vh solid #6a6363;
  padding-top: 13vh;
}

.header h5 {
  font-size: 16px;
  margin-bottom: 0.5vh;
}

.mainText {
  color: var(--first-color);
}

.header h1 {
  font-size: 48px;
  margin-bottom: 1vh;
}

/* Social links */
.socialCaption {
  font-style: italic;
  font-size: 1.5rem;
  margin-right: 1vh;
  color: var(--first-color);
}

.socials {
  display: flex;
  justify-content: center;
  margin-top: 1.2vh;
}

.socials a {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-right: 1.4vh;
  color: var(--first-color);
  text-decoration: none;
  font-size: 24px;
  transition: color 0.3s ease, transform 0.3s ease;
}

.socials a:hover {
  transform: scale(1.2);
}

/* About */
.aboutme {
  padding-top: 6vh;
  max-width: 100%;
  margin: 0 auto;
  line-height: 1.6;
}

.aboutme h4 {
  font-size: 18px;
  color: var(--text-color);
}

/* Buttons */
.buttons {
  padding-top: 6vh;
  padding-bottom: 3vh;
}

.button {
  background-color: var(--first-color);
  width: 150px;
  height: 51px;
  color: white;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  margin: 4px 8px;
  cursor: pointer;
  border-radius: 16px;
  margin-top: 1vh;
  transition: background-color 0.3s ease-in-out, opacity 0.3s ease-in;
}

.button:hover {
  background-color: #ff491b76;
}

/* Featured */
.featured {
  text-align: center;
  padding-top: 3vh;
}

.featuredProjects {
  display: flex;
  justify-content: space-between;
  margin: 10vh;
  margin-bottom: 17vh;
}

.project {
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.6);
  width: 30%;
  background-color: var(--card-color);
  padding: 1.5rem;
  border-radius: 1.3rem;
  border: 1px solid transparent;
  text-align: center;
  overflow: hidden;
  transition: transform 0.3s ease-in-out, border-color 0.3s ease-in-out;
}

.featuredProjects:hover .project:not(:hover) {
  transform: scale(0.8);
}

.project:hover {
  border-color: var(--first-color);
  transform: scale(1.35);
  cursor: pointer;
}

.projectImage {
  width: 100%;
  height: 65%;
  border-radius: 0.3rem;
  margin-bottom: 1rem;
}

.project h3 {
  font-size: 18px;
  color: var(--text-color);
}

@media screen and (max-width: 1024px) {
  .header h1 {
    font-size: 40px;
  }

  .featuredProjects {
    flex-direction: column;
    align-items: center;
  }

  .project {
    width: auto;
    padding: 0.5rem;
    margin-bottom: 2rem;
  }

  .project h3 {
    font-size: 12px;
  }
}

@media screen and (max-width: 600px) {
  .socials a:hover {
    transform: none;
  }

  .featuredProjects:hover .project:not(:hover) {
    transform: none;
  }

  .project:hover {
    transform: none;
  }
  .header h1 {
    font-size: 33px;
  }

  .aboutme {
    padding-top: 4vh;
  }

  .aboutme h4 {
    font-size: 14px;
  }

  .buttons {
    padding-top: 4vh;
    padding-bottom: 2vh;
  }

  .button {
    width: 120px;
    height: 40px;
    font-size: 14px;
    margin-top: 0.5vh;
  }
}
